@import '../../styles/variables';
@import '../../styles/mixins';

.container {
  background: $color-neutral90;
  background-size: cover;
  z-index: 5;
}

.wrapper {
  display: flex;
  width: 100%;
  height: 560px;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;

  align-items: center;
  transition: all linear 0.4s;

  .button {
    cursor: pointer;
  }

  @include media-breakpoint-up('lg') {
    @media all and (min-width: 1600px) {
      height: 720px;
    }
  }
  @include media-breakpoint-up('xxl') {
    height: 900px;
  }

  button {
    height: 36px;
    border: 1px solid $color-yellow;
    position: absolute;
    bottom: 40px;
    padding: 20px 25px;
    overflow: hidden;
    background: rgba(17, 18, 19, 0.7);
    transition: all linear 0.2s;
    display: flex;
    align-items: center;

    @media all and (max-width: 992px) {
      bottom: 75px;
    }

    p {
      font-family: Mulish;
      font-style: normal;
      font-weight: 900;
      font-size: 16px;
      line-height: 32px;
      color: $color-white;
      margin-left: 19px;
    }

    img {
      cursor: pointer;
      filter: brightness(100);
    }

    @include media-breakpoint-up('lg') {
      p {
        font-family: Mulish;
        font-style: normal;
        font-weight: 900;
        font-size: 16px;
        line-height: 32px;
        margin-left: 19px;
        color: $color-white;
      }
    }
    @include media-breakpoint-down('lg') {
      &:active {
        background: $color-yellow;
        p {
          color: $color-neutral90;
        }
        img {
          filter: brightness(0);
        }
      }
    }
    @include media-breakpoint-up('lg') {
      &:hover {
        background: $color-yellow;
        p {
          color: $color-neutral90;
        }
        img {
          filter: brightness(0);
        }
      }
    }
  }
}

.controlsContainer {
  position: absolute;
  margin-top: -75px;
  right: 20%;

  @media all and (max-width: 992px) {
    position: static;
    margin-top: -40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.video {
  position: absolute;
  width: 2000px;
  height: 2000px;
  object-fit: cover;
  z-index: -1;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

.mute {
  margin-left: 25px;
  cursor: pointer;
  color: white;
  width: 30px;
  height: 30px;
  padding: 6px;
  filter: brightness(100);
  @media all and (max-width: 992px) {
    width: 45px;
    margin-left: 0px;
  }
}
