@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.smallText {
  font-size: 16px;
  line-height: 32px;
  color: $color-white;

  @include media-breakpoint-up("lg") {
    font-size: 26px;
    line-height: 40px;
  }

  .quotes {
    display: inline !important;
    margin-right: 15px;

    @include media-breakpoint-down("sm") {
      width: 23px;
    }
  }
}
