@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.containerSelector {
  display: flex;
  justify-content: space-between;
  padding: 16px 0;
}

.IconTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
}

.title {
  width: 100%;
  font-weight: 900;
  font-size: 20px;
  line-height: 26px;
  margin-left: 16px;
  transition: all 0.5s;
  cursor: pointer;
}
.activeTitle {
  color: $color-yellow;
  transition: all 0.5s;
}

.icon {
  transition: 0.5s;
  transform: rotate(0deg);
}
.activeIcon {
  transition: 0.5s;
  transform: rotate(180deg);
}
.contentContainer {
  overflow: hidden;
  max-height: 0;
  transition: all 0.5s ease-in-out;
}

.active {
  max-height: 450px;
  margin-bottom: 24px;
}

.text {
  font-size: 16px;
  line-height: 32px;
}

.buttonContainer {
  margin-bottom: 12px;
}
