@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.mainContainer {
  border: 1.5px solid #2f3237;
  box-sizing: border-box;
  
  width: 264px;
  @include media-breakpoint-down('md') {
    width: 360px;
    max-width: 100%;
    margin: auto;
    //margin: 0 27px;
    margin-bottom: 64px;
    border-left: none;
    border-right: none;
      
    @include media-breakpoint-up('sm') {
      width: 100%;
    }
  }
  @include media-breakpoint-down("lg") {
    @include media-breakpoint-up("md") {
      width: 100%;
    }
  }
}

.containerSelector {
  display: flex;
  justify-content: space-between;
  padding: 16px 0;
  &:hover {
    cursor: pointer;
  }
  
}

.IconTitleContainer {
  display: flex;
}

.title {
  font-weight: 900;
  font-size: 20px;
  line-height: 26px;
  margin-left: 16px;
  transition: all 0.5s;
}
.activeTitle {
  color: $color-yellow;
  transition: all 0.5s;
}

.icon {
  transition: 0.5s;
  transform: rotate(0deg);
  margin-right: 20px;
}
.activeIcon {
  transition: 0.5s;
  transform: rotate(180deg);
}
.contentContainer {
  overflow: hidden;
  max-height: 0;
  transition: all 0.5s ease-in-out;
  margin: 0px 16px;
}

.active {
  max-height: 400px;
  margin: 16px 16px 0;
  transition: all 0.5s ease-in-out;
}

.text {
  font-size: 16px;
  line-height: 32px;
}

.buttonContainer {
  margin-bottom: 12px;
}

.containerItem {
  display: flex;
  margin-bottom: 16px;
  
}

.textCheckBox {
  font-size: 16px;
  line-height: 24px;
  &:hover {
    cursor: pointer;
  }
}
