@import "../../styles/mixins.scss";
@import "../../styles/variables.scss";

.button {
  display: flex;
  justify-content: flex-end;

  @include media-breakpoint-down("lg") {
    justify-content: flex-start;
    margin-bottom: 40px;
  }
}

.image {
  margin: 0 !important;
}

.card {
  position: relative;
  display: flex;
  max-width: 360px;
  min-height: 480px;
  max-height: 480px;
  border: solid 1px $color-neutral70;
  padding: 30px 28px;
  flex-direction: column;
  justify-content: flex-end;
  margin: 0 !important;
  @include media-breakpoint-down("xl") {
    max-width: 300px;
    min-height: 400px;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    border-color: $color-white;
    cursor: pointer;
    .BtnLearnMore {
      color: $color-yellow;
    }
  }

  h3 {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 26px;
    font-weight: bold;
  }

  p {
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 20px;
    color: $color-gray;
  }

  * {
    position: relative;
    z-index: 2;
  }

  > img {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  @media screen and (min-width: 1820px) {
    margin: 0 !important;
  }
}

.title {
  font-weight: 900;
  font-size: 32px;
  line-height: 38px;
}
