@import "../../styles/variables";
@import "../../styles/mixins";

.divScrollUp {
    visibility: hidden;
    position: fixed;
    right: 40px;
    transition: all .3s;
    transform: rotate(-90deg);
    background-color: $color-neutral90;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    z-index: 10;

    @include media-breakpoint-down("md"){
        right: 27px;
    }
}

.hidden {
    bottom: -60px;
    visibility: hidden;
}

.showed {
    bottom: 40px;
    visibility: visible;

    @include media-breakpoint-down("md"){
        bottom: 27px;
    }
}