@import "../../styles/variables";
@import "../../styles/mixins";

.card {
  border: solid 1px $color-neutral70;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 0;
  cursor: pointer;

  @include media-breakpoint-down('lg') {
    margin-bottom: 20px;
  }

  &:hover {
    border-color: $color-white;
    .cardBtn {
      color: $color-yellow;
    }
  }
}

.emptyCard {
  display: none;
}

.icon {
  display: flex;
}

.name {
  display: flex;
  height: 47px;
  margin: 25px 0;
  align-items: center;
  
  h3 {  
    text-align: center;
    font-size: 16px;
    line-height: 24px;
  }
}