@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.container {
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  grid-template-rows: 1fr 1fr;
  $margin-left: 16px;

  .image {
    grid-row: span 2;
    justify-self: center;
  }

  .name {
    margin-left: $margin-left;
    margin-bottom: 4px;
    font-size: 20px;
    line-height: 26px;
    align-self: flex-end;
    color: $color-white;
  }

  .subtext {
    margin-left: $margin-left;
    color: $color-gray;
    font-size: 16px;
    line-height: 32px;
  }
}
