@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.header {
  position: fixed;
  z-index: 3;
  width: 100%;
  transition: all .3s;
  border-bottom: solid 1px transparent;
  @include media-breakpoint-down("md"){
    .containerHeader {
      padding-right: 27px !important;
      padding-left: 27px !important;
    }
  }
}

.showBackground {
  background: $color-neutral90;
  border-color: $color-neutral70;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  height: 80px;
}

.logo {
  cursor: pointer;
  line-height: 0;
}

.toggleLanguage {
  background: none;
  border: none;
  padding: 0;
  line-height: 0;
  font-size: 16px;
  font-weight: bold;
  color: $color-white;
  margin-left: auto;
  text-transform: uppercase;

  img {
    margin-right: 10px;
    filter: brightness(0) invert(1);
  }

  &:hover {
    color: $color-yellow;
    img {
      filter: none;
    }
  }
}

.toggleMenu {
  padding-top: 6px;
  border: none;
  background: none;
  margin-left: 20px;
  img {
    filter: brightness(0) invert(1);
    &:hover {
      filter: none;
    }
  }
}