@import '../../styles/mixins';
@import '../../styles/variables';

.contentContainer {
  max-width: 268px;
  text-align: center;

  .containerItem {
    display: flex;
    flex-direction: column;
    @media all and (max-width: 389px) {
      margin: auto;
      margin-bottom: 56px;
    }
    margin-bottom: 56px;
    width: 228px;

    @media all and (min-width: 390px) and (max-width: 413px) {
      width: 155px;
    }

    @include media-breakpoint-up('lg') {
      width: 228px;
      margin-right: 72px;

      &:nth-of-type(4) {
        margin-right: 0;
      }
    }
  }

  .imgContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16px;
  }

  .text {
    p {
      font-family: Mulish;
      font-size: 16px;
      line-height: 32px;
      text-align: center;
      color: $color-gray;
    }
  }
}
