@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.cmp-button {
  display: flex;
  align-items: center;
  padding: 15px;
  font-size: 16px;
  font-weight: 900;
  line-height: 32px;
  height: 48px;
  color: $color-white;
  border: solid 1px transparent;
  background: transparent;
  flex-direction: row-reverse;
}

.left {
  flex-direction: row-reverse;

  img,
  svg {
    margin-right: 16px;
  }
}

.right {
  flex-direction: row;

  img,
  svg {
    margin-left: 10px;
  }
}

.icon {
  img,
  svg {
    margin-left: 0px;
    margin-right: 0px;
  }
}

.primary {
  border-color: $color-yellow;

  &:hover,
  &:active {
    color: $color-neutral90;
    background: $color-yellow;

    img {
      filter: brightness(0);
    }
  }

  &.light {
    color: $color-neutral90;
  }
}

.secondary {
  border-color: $color-white;

  img {
    filter: none;
  }

  &:hover,
  &:active {
    color: $color-neutral90;
    background: $color-white;

    img {
      filter: brightness(0);
    }
  }

  &.light {
    img {
      filter: brightness(0);
    }
    color: $color-neutral90;
    border-color: $color-neutral90;

    &:hover {
      color: $color-white;
      background: $color-neutral90;

      // make it white
      img {
        filter: brightness(255) saturate(0);
      }
    }
  }
}

.tertiary {
  padding: 0;

  &:hover,
  &:active {
    color: $color-yellow !important;
  }

  &.light {
    color: $color-neutral70;
  }
}
