@import "../../styles/variables";
@import "../../styles/mixins";

.ImageContainer {
  height: 360px;
  @include media-breakpoint-up("lg") {
    height: 100%;
    width: 100%;
  }
}

.imageBackGround {
  background-image: url("../../assets/arrow-right-north-white.png");
  background-repeat: no-repeat;
  background-size: contain;
  height: 360px;
  max-height: 100%;
  @include media-breakpoint-up("lg") {
    height: 100%;
    width: 100%;
  }
  @include media-breakpoint-up("xl") {
    max-height: 464px;
    max-width: 464px;
  }
  display: flex;
}

.text {
  margin-top: 40px;
  @include media-breakpoint-up("lg") {
    margin-top: 0;
  }
  font-size: 16px;
  line-height: 24px;
  color: $color-neutral70;
  span {
    font-weight: 900;
  }
}

.title {
  align-self: center;
  color: $color-neutral90;
  font-size: 40px;
  line-height: 40px;
  font-weight: 900;
  @media all and  (max-width: 380px) {
      margin-top: -60px;
  }
  @include media-breakpoint-up("lg") {
    font-size: 56px;
    line-height: 56px;
    margin-left: 80px;
  }
}
