@import '../../styles/mixins';

.button {
  display: flex;
  justify-content: end;
  height: 100%;

  @include media-breakpoint-down('lg') {
    justify-content: start;
    margin-bottom: 70px;
    max-height: 40px;
  }
}

.text {
  margin-bottom: 42px !important;
  @include media-breakpoint-up('lg') {
    width: 610px !important;
  }
  width: 360px;
  max-width: 100%;
}
