@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.compassblog {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  gap: 1em;
  padding: 3em 1em;
  background-color: $color-neutral90;

  .row {
    display: flex;
    flex-direction: row;

    justify-content: space-between;

    @include media-breakpoint-down("sm") {
      flex-direction: column;

      button {
        width: fit-content;
      }
    }
  }
}
