@import '../../styles/mixins';

.listContainer {
  padding: 120px 0;

  @include media-breakpoint-down('md') {
    padding: 80px 0 24px;
  }

  .title {
    padding: 0 14px;

    margin: 0 27px 56px 15px;
    font-family: Mulish;
    font-weight: 900;
    font-size: 32px;
    line-height: 38px;

    @include media-breakpoint-up('lg') {
      margin: 56px 0 56px 12px;
    }
  }

  .itemsContainer {
    max-width: 1248px;
    @media all and (max-width: 1330px) {
      padding-left: 30px;
    }
  }

  .titleContainer {
    @include remove-padding;
  }
}
