@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.container {
  background: $color-neutral90;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.4);

  @include media-breakpoint-down("sm") {
    margin-right: -2.5px;
}
}

.containerWrapper {
  box-sizing: border-box;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding: 0 12px;

  @include media-breakpoint-up("xxl") {
    max-width: 1140px;
  }

  @include media-breakpoint-down("xxl") {
    max-width: 1140px;
  }

  @include media-breakpoint-down("xl") {
    max-width: 960px;
  }

  @include media-breakpoint-down("lg") {
    max-width: 740px;
  }

  @include media-breakpoint-down("md") {
    max-width: 768px;
    padding: 0 26px;
  }

  @include media-breakpoint-down("sm") {
    max-width: 540px;
    padding: 0 12px;
  }
}

.wrapper {
  display: flex;
  width: 100%;
  height: 640px;
  flex-direction: column;
  justify-content: flex-end;

  @include media-breakpoint-down("sm") {
    justify-content: center;
    width: 360px;
    max-width: 100%;
    margin: 0 auto;
  }

  .title {
    height: 512px;
    max-width: 512px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    padding: 182px 4px 0;
    
    @include media-breakpoint-down("sm") {
      height: 300px;
      padding: 90px 4px 0px;
      margin-right: -40px;
      max-width: 400px;
    }

    @media screen and (max-width: 375px) {
      height: 300px;
      padding-top: 90px;
      margin-right: -40px;
      max-width: 391px;
    }

    @media screen and (max-width: 320px) {
      height: 300px;
      padding-top: 90px;
      margin-right: -40px;
      max-width: 336px;
    }
  }
}

.text {
  width: 350px;
  font-family: Mulish;
  color: $color-white;
  font-weight: 900;
  font-size: 48px;
  line-height: 48px;

  @include media-breakpoint-down("sm") {
    font-size: 40px;
    line-height: 40px;
  }

  @include media-breakpoint-up("md") {
    line-height: 48px;
    font-size: 48px;
  }
}

.button {
  margin-top: 35px;

  @include media-breakpoint-down("sm") {
    margin-top: 40px;
  }
}

.containerWrapperCase {
  box-sizing: border-box;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding: 0 12px;

  @include media-breakpoint-up("xxl") {
    max-width: 1140px;
  }

  @include media-breakpoint-down("xxl") {
    max-width: 1140px;
  }

  @include media-breakpoint-down("xl") {
    max-width: 960px;
  }

  @include media-breakpoint-down("lg") {
    max-width: 740px;
  }

  @include media-breakpoint-down("md") {
    max-width: 768px;
    padding: 0 26px;
  }

  @include media-breakpoint-down("sm") {
    max-width: 540px;
    padding: 0 12px;
  }
}

.wrapperCase {
  display: flex;
  width: 100%;
  height: 640px;
  flex-direction: column;
  justify-content: center;
}

.textCase {
  width: 325px;
  font-family: Mulish;
  font-weight: 900;
  color: $color-yellow;
  font-weight: 900;
  font-size: 40px;
  line-height: 40px;
  @include media-breakpoint-up("md") {
    line-height: 48px;
    font-size: 48px;
  }
  @include media-breakpoint-up("lg") {
    width: 900px;
    line-height: 60px;
  }
}
