@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.menu {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;

  a {
    margin-right: 20px;
  }

  @include media-breakpoint-down("lg") {
    position: absolute;
    top: 80px;
    flex-direction: column;
    background: $color-neutral90;
    align-items: flex-start;
    padding-top: 20px;

    a {
      margin-bottom: 20px;
    }

    &:before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: $color-neutral90;
    }
  }
}
