@import '../../styles/mixins';
@import '../../styles/variables';

.contentContainer {
  margin: 0 25px;

  .target {
    margin-top: 22px;
  }
  .containerItem {
    display: flex;
    flex-direction: column;
    margin-bottom: 56px;

    @include media-breakpoint-down('sm') {
      margin: 56px auto 0;
    }

    @include media-breakpoint-up('lg') {
      width: 252px;
      margin-right: 10px;

      p {
        font-size: 14px;
        line-height: 20px;
      }
      &:nth-of-type(4) {
        margin-right: 0;
      }
    }
  }

  .imgContainer {
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 16px;

    img {
      margin: 0;
    }
  }

  .containertarget {
    margin-top: 22px;
    font-weight: 900;
    font-size: 16px;
    line-height: 32px;
  }

  .text {
    font-family: Mulish;
    font-size: 16px;
    line-height: 32px;
    color: $color-gray;
  }
}
