@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.imagePlaceholder {
  display: flex;
  align-items: center;
  height: 100%;

  @include media-breakpoint-down('lg') {
    height: calc(100% + 80px);
  }
}

.container {
  position: relative;
  @include media-breakpoint-down('lg') {
    width: 100%;
  }

  img {
    position: relative;
    z-index: 2;
    max-width: 100%;
    @include media-breakpoint-down('lg') {
      @include media-breakpoint-up('md') {
        width: 100%;
        height: auto;
      }
    }
    @include media-breakpoint-down('sm') {
      width: 360px;
      height: 360px;
      object-fit: cover;
      object-position: center;
    }
  }

  &:before {
    content: '';
    background: $color-yellow;
    height: calc(100% + 80px);
    width: 100%;
    position: absolute;
    top: -40px;
    left: 120px;

    @include media-breakpoint-down('lg') {
      left: 220px;
    }

    @media (max-width: 1440px) {
      width: 1000%;
    }
  }
}

.containerInverse {
  position: relative;
  @include media-breakpoint-up('lg') {
    width: 100%;
  }

  img {
    position: relative;
    z-index: 2;
    max-width: 100%;

    @include media-breakpoint-down('lg') {
      width: 100%;
      max-width: 108%;
    }
  }

  &:before {
    content: '';
    background: $color-yellow;
    height: calc(100% + 80px);
    width: 100%;
    position: absolute;
    top: -40px;
    right: 120px;

    @include media-breakpoint-down('lg') {
      right: 220px;
    }

    @media (max-width: 1440px) {
      width: 1000%;
    }
  }
}

.weAreGptwImageContainer {
  @include media-breakpoint-up('lg') {
    width: 600px;
  }
}

.containerInverseFullWidth {
  position: relative;
  @include media-breakpoint-up('lg') {
    width: 100%;
  }

  img {
    position: relative;
    z-index: 2;
    max-width: 100%;

    @include media-breakpoint-down('lg') {
      width: 100%;
    }
  }

  &:before {
    content: '';
    background: $color-yellow;
    height: calc(100% + 80px);
    width: 100%;
    position: absolute;
    top: -40px;
    right: 55px;

    @include media-breakpoint-down('lg') {
      right: 220px;
    }

    @media (max-width: 1440px) {
      width: 1000%;
    }
  }
}
