@import '../../styles/mixins';
@import '../../styles/variables';

.card {
  max-width: 360px;
  border: solid 1px $color-neutral70;
  background: $color-neutral80;
  padding: 20px;

  @include media-breakpoint-down('xl') {
    max-width: 300px;
  }

  .buttonReadMore {
    color: $color-white;
  }
  &:hover {
    cursor: pointer;
    border-color: $color-white;
    
    .buttonReadMore {
      color: $color-yellow;
    }
  }

  h3 {
    color: $color-gray;
    margin-bottom: 30px;
  }
}

.text {
  padding-top: 16px;
  .cardText{
    min-height: 224px;
    font-size: 13px;
    line-height: 28px;
    color: $color-gray;
  }
}

img {
  margin: 0;
}