@import '../../styles/mixins.scss';
@import '../../styles/variables.scss';

.searchBox {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;

  border: none;
  box-sizing: border-box;

  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px auto 24px;

  @include media-breakpoint-down('md') {
    max-width: 100%;
    padding: 0 27px;
  }

  input {
    border: 1px solid #8f96a3;
    background-color: transparent;
    box-sizing: border-box;

    font-family: Mulish;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 32px;

    display: flex;
    align-items: center;

    color: #c2c9d6;

    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 1;
    margin: 0px 0px;
    padding: 4px 12px 4px 36px;

    outline: none;

    // background-image: url("/search-icon.svg");
    background-repeat: no-repeat;
    background-position: 12px center;
  }

  input:focus {
    border-color: $color-yellow;
  }
}
