@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.ourOffices {
    display: flex;
    flex-direction: column;
    background-color: $color-neutral80;

    h2 {
        font-family: Mulish;
        font-style: normal;
        font-weight: 900;
        font-size: 32px;
        line-height: 38px;
        color: $color-white;
        margin-bottom: 32px;
    }

    p {
        max-width: 906px;
        font-family: Mulish;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 32px;
        margin-bottom: 32px;
        color: $color-gray;
    }

    .divMap{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            max-width: 408px;
            width: 124%;

        @include media-breakpoint-up('sm') {
            max-width: 983px;
            width: 100%;
          }
        }
    }
}