@import "../../styles/mixins";

.technologyPartners {
  @include media-breakpoint-up("lg") {
    padding: 120px 0 120px;
  }
  padding: 120px 0px 80px;
}

.button {
  display: flex;
  justify-content: flex-end;

  @include media-breakpoint-down("lg") {
    justify-content: flex-start;
    margin-bottom: 70px;
    max-height: 40px;
  }
}

.card {
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: fit-content;
  @include media-breakpoint-up("lg") {
    width: fit-content;
    margin-right: 60px;
    min-height: 40px;
    justify-content: flex-start;
  }

  img {
    @include media-breakpoint-up("lg") {
      height: 100%;
      width: fit-content;
    }
    width: 150px;
    max-height: 40px;
    display: flex;
    justify-content: space-between;
    margin: 0;
  }
}

.text {
  margin-bottom: 32px !important;
  @include media-breakpoint-up("lg") {
    width: 496px !important;
  }
  width: 360px;
  max-width: 100%;
}

.mobContainer {
  display: flex;
  flex-direction: row;
  height: 48px;
  margin: 0 auto;
  min-width: 168px;
  margin-bottom: 40px;
  justify-content: center;
  .cardMobile {
    max-width: 100%;
  }

  .imageMob {
    max-width: 168px;
  }
}

.mobContainerLastItem {
  display: flex;
  flex-direction: row;
  height: 48px;
  margin: 0 auto;
  min-width: 100%;
  margin-bottom: 40px;
  justify-content: center;
  .cardMobile {
    max-width: 100%;
  }

  .imageMob {
    max-width: 168px;
  }
}

.partners {
  height: 40px;

  object-fit: contain;
  object-position: center;
  padding: 0;
  margin: 0;

  &:hover,
  &:active,
  &:focus {
    cursor: pointer;
    filter: brightness(2);
  }

  @include media-breakpoint-up("md") {
    margin-left: 10px;
  }
}
