@import '../../styles/variables';
@import '../../styles/mixins';

.cardContainer {
  @media all and (min-width: 390px) and (max-width: 413px) {
    width: 155px !important;
    flex: none !important;
  }
  @media all and (min-width: 414px) and (max-width: 767px) {
    width: 168px !important;
    flex: none !important;
  }
  @media all and (min-width: 768px) and (max-width: 992px) {
    width: 168px !important;
    flex: none !important;
    margin-right: 10px !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 0;
  margin-bottom: 64px;
  border: 1.5px solid $color-neutral70;
  box-sizing: border-box;
  @include media-breakpoint-down('sm') {
    width: 168px;
    max-width: 100%;
    margin-bottom: 24px;
  }
  &:hover {
    border-color: $color-white;
    cursor: pointer;
    .textBtnCard {
      color: $color-yellow;
    }
  }
}

.icon {
  display: flex;
}

.name {
  height: 48px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 12px 0;

  h3 {
    text-align: left;
    font-size: 14px;
    line-height: 20px;
    font-weight: 900;
    text-align: center;
  }
}
