@import '../../styles/variables';
@import '../../styles/mixins';

.footer {
  background: $color-neutral90;
  border-top: solid 1px $color-neutral70;
  overflow: hidden;
  @include media-breakpoint-down('sm') {
    .footerContainer {
      padding-left: 27px !important;
      padding-right: 27px !important;
    }
  }
}

.footerContainer {
  @include media-breakpoint-up('lg') {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
.container {
  display: flex;
  justify-content: space-between;
  padding: 45px 0;

  @include media-breakpoint-down('lg') {
    flex-direction: column;
    > div {
      margin-bottom: 50px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.brand {
  max-width: 176px;
}

.compass {
  margin: 7px 0 15px;
}

.bottom {
  display: flex;
  justify-content: space-between;
  border-top: solid 1px $color-neutral70;
  padding: 20px 0;
  align-items: center;

  @include media-breakpoint-down('lg') {
    flex-direction: column;
    text-align: center;
  }
}

.copyright {
  color: $color-white;
  font-size: 12px;
}

.bottomtLinks {
  display: flex;

  @include media-breakpoint-down('lg') {
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px;
  }

  a {
    color: $color-white;
    font-size: 12px;
    margin-left: 10px;

    &:first-child {
      margin-left: 0px;
    }
    &:hover {
      text-decoration: underline;
    }

    @include media-breakpoint-down('lg') {
      margin-bottom: 5px;
    }
  }
}
.groupSocialLinks {
  width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #111213;
  padding: 10px;
}
