@import '../../styles/variables';
@import '../../styles/mixins';

.language {
  position: absolute;
  top: 80px;
  background: $color-neutral90;
  align-items: flex-start;
  padding-top: 20px;
  @include media-breakpoint-down('lg') {
  }
  a {
    margin-bottom: 10px;
  }

  &:before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: $color-neutral90;
  }
}

.button {
  display: flex;
  align-items: center;
  border: none;
  background: transparent;
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 20px;
  color: $color-white;

  h3 {
    font-family: Mulish;
    font-style: normal;
    font-weight: 900;
    font-size: 26px;
    line-height: 32px;
    color: $color-white;
  }

  img {
    margin-right: 15px;
    width: 24px;
    height: 24px;
  }

  @include media-breakpoint-down('lg') {
    font-size: 16px;
  }
}

.center {
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: fixed;
  min-width: 270px;
}

.TextLanguage {
  &:hover {
    color: $color-yellow;
  }
}
