@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.customDropdown {
  display: flex;
  flex-direction: column;

  transition: all 0.2s;

  .field {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: $color-neutral70;
    background-color: #fff;
    border: 1px solid #8f96a3;
    outline: none;

    font-size: 16px;
    line-height: 32px;
    padding: 10px;

    height: 48px;
    cursor: pointer;

    .icon {
      color: $color-neutral90;
      user-select: none;

      &.iconInvert {
        transform: scaleY(-1);
      }
    }

    &.open {
      border: 1px solid $color-yellow;
    }

    &.error {
      border: 1px solid $status-error;
    }
  }

  .errorText {
    color: $status-error;
    font-size: 12px;
    line-height: 16px;
    margin-top: 4px;
  }

  .dropdown {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background-color: #fff;
    border: 1px solid #8f96a3;
    border-top: none;

    &.closed {
      display: none;
    }

    // estilo padrÃ£o para items no dropdown, talvez remover no futuro
    & > * {
      width: 100%;
      color: $color-neutral90;
      padding: 12px 12px;

      &:hover {
        background-color: #000;
        color: $color-white;
      }
    }
  }
}
