@import '../../styles/variables.scss';
@import '../../styles/mixins';

.textPlaceholder {
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;

  h2 {
    font-size: 32px;
    line-height: 38px;
    font-weight: 900 !important;
    margin-bottom: 32px;
    color: $color-white;
  }

  p {
    color: $color-gray;
    font-size: 16px;
    line-height: 32px;
    margin-bottom: 24px;
  }

  span {
    font-weight: 400 !important;
  }

  button {
    @include media-breakpoint-down('lg') {
      margin-bottom: 30px;
    }
  }
}