@import '../../styles/mixins';

.container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  position: absolute;
  background: none;
  border: none;
  right: -40px;
  top: 0px;
  img {
    filter: brightness(0) invert(1);
    &:hover {
      filter: none;
    }
  }
  @include media-breakpoint-down('lg') {
    right: 5px;
    top: -40px;
  }
}

.wrapper {
  position: relative;
  width: 930px;
  height: 523px;
  padding: 0 12px;

  @include media-breakpoint-down('lg') {
    width: 720px;
    height: 405px;
  }

  @include media-breakpoint-down('md') {
    width: 540px;
    height: 303px;
  }

  @include media-breakpoint-down('sm') {
    width: 350px;
    height: 196px;
  }
}

.video {
  width: 100%;
  height: 100%;
}
