@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@400;700;900&display=swap');
@import 'variables';
@import 'slick';
@import 'mixins';

html,
body {
  padding: 0;
  margin: 0;
  background: $color-neutral90;
  color: $color-white;
  font-family: 'Mulish', sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;

  p {
    margin: 0;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

.clearfix {
  overflow: auto;
}

.clearfix::after {
  content: '';
  clear: both;
  display: table;
}

.backgroundGray {
  background-color: $color-neutral80;
}

.backgroundYellow {
  background-color: $color-yellow;
}

.noOverflow {
  overflow: hidden;
}
.void {
  display: none !important;
}

.imageTextTextMobCorrection {
  @include media-breakpoint-down('md') {
    padding-left: 27px !important;
    padding-right: 27px !important;
  }
}

.genericContainer {
  padding: 96px 0;
}

.removePadding {
  @include media-breakpoint-up('lg') {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.removeMobPadding {
  @include media-breakpoint-down('md') {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.carouselContainerMobAdjust {
  @include media-breakpoint-down('md') {
    padding-left: 27px !important;
  }
}

.mobPaddingCarousel {
  @media all and (max-width: 380px) {
    padding-left: 24px !important;
    padding-right: 24px !important;
  }
}

.mobWidth {
  @include media-breakpoint-down('md') {
    width: 360px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
}

.video {
  z-index: 0;
  position: absolute;
  object-fit: cover;
  height: 560px;

  @include media-breakpoint-up('lg') {
    width: 1360px;
    height: 560px;
    @media all and (min-width: 1600px) {
      height: 720px;
    }
  }
  @include media-breakpoint-up('sm') {
    width: 100%;
  }
  @include media-breakpoint-up('xl') {
    width: 100%;
  }
  @include media-breakpoint-down('sm') {
    width: 100%;
    object-fit: contain;
    background: #000000;
  }
  @include media-breakpoint-up('xxl') {
    height: 900px;
  }
}

.videoOverlay {
  background: rgba(0, 0, 0, 0.1);
  height: 560px;
  width: 100%;
  position: absolute;

  @include media-breakpoint-up('lg') {
    top: 80px;
  }
}

#exemplo {
  width: 500px;
  height: 500px;
  background-color: blue;
  position: absolute;
  top: 0;
  left: 0;
}
